.slider-container {
  width: 90%;
  margin: auto;
  height: 500px;
}

.slider-item {
  padding: 15px;
}
.slick-track{
    height: 340px;
}
 .slick-arrow.slick-next:before{
    font-size: 30px;
      background: -webkit-linear-gradient(135deg , #aa70e0 , #7059e2);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

 }
  .slick-arrow.slick-prev:before{
    font-size: 30px;
       background: -webkit-linear-gradient(135deg , #aa70e0 , #7059e2);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

 }

.testimonial-card {
  background: white;
  padding: 10px;
  height: 270px;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
  transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;
  opacity: 0.5; /* Make non-active slides less opaque */
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  text-align: start;
  align-items: center;
  gap: 10px;
  /* border: 2px solid #7059e2; */
}
.review-section{
  width: 90%;
  padding: 10px;
  height: 200px;
  border-radius: 10px;
  font-size: 16px;
  color: #000;
}



.author-info {
  width: 90%;
  display: flex;
  padding: 10px;
  border-radius: 10px;
    background: linear-gradient(135deg , #aa70e0 , #7059e2);
  align-items: center;
 color: #fff;
}

.author-info img {
  border-radius: 50%;
  width: 50px;
  height: 50px;
  margin-right: 10px;
}

.author-info h4 {
  margin: 0;
  font-size: 1.1em;
  color: #fff;
}

.author-info p {
  margin: 0;
  font-size: 0.9em;
  color: #fff;
}

/* Style the active (center) slide */
.slick-center .testimonial-card {
  transform: scale(1.1);
  opacity: 1; /* Full opacity for the active slide */
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

