.contactFormWrapper{
    width: 90%;
    
    display: flex;
  
    flex-direction: column;
     align-items: center;
    height: 500px;
   background: rgba(255, 255, 255, 0.225);
box-shadow: rgba(0, 0, 0, 0.14) 0px 1px 4px;
backdrop-filter: blur( 10px );
-webkit-backdrop-filter: blur( 0px );
    border-radius: 20px;
    justify-content: center;
}
.contactFormWrapper form{
    display: flex;
    flex-direction: column;
    width: 80%;
    gap: 20px;
}
.contactFormWrapper form input{
    width: 100%;
    padding: 15px;
    border: 1px solid #ccc;
    border-radius: 50px;
      border: 0;
    outline: 0;
}
.contactFormWrapper form textarea{
    width: 100%;
    height: 150px;
    padding: 15px;
    border: 1px solid #ccc;
    border-radius: 20px;
    border: 0;
    outline: 0;
    resize: none;
}
.contactFormWrapper form button{
    padding: 10px;
        background: linear-gradient(135deg , #aa70e0 , #7059e2);
    color: #fff;
   border: 0;
    border-radius: 50px;
    cursor: pointer;
    font-size: 19px;
}
.contactFormWrapper form h3{
    margin-left: 20px;
}
.contactFormWrapper form span{
         background: -webkit-linear-gradient(135deg , #aa70e0 , #7059e2);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

}
@media(max-width:768px){
    .contactFormWrapper{
    width: 100%;
    

}

}
@media(max-width:580px){
    
.contactFormWrapper{
    width: 100%;
    
  
    height: 400px;

}
.contactFormWrapper form{
    width: 90%;
    gap: 10px;
}
.contactFormWrapper form input{
    width: 100%;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 50px;
      border: 0;
    outline: 0;
}
.contactFormWrapper form textarea{
    width: 100%;
    height: 130px;
    padding: 15px;
    border: 1px solid #ccc;
    border-radius: 20px;
    border: 0;
    outline: 0;
    resize: none;
}
.contactFormWrapper form button{
    padding: 10px;
        background: linear-gradient(135deg , #aa70e0 , #7059e2);
    color: #fff;
   border: 0;
    border-radius: 50px;
    cursor: pointer;
    font-size: 19px;
}
.contactFormWrapper form h3{
    margin-left: 20px;
}


}