.slider {
  position: relative;
  width: 500px;
  height: 500px;
  margin: auto;
  overflow: hidden;
}



.slider img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: opacity 1s ease-in-out;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.slider img.active {
  opacity: 1;
  position: relative;
  z-index: 1;
}

.indicators {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
}

.indicators span {
  width: 10px;
  height: 10px;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  margin: 0 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.indicators span.active {
  background-color: rgba(255, 255, 255, 1);
}
@media(max-width:768px){
  .slider {
    width: 100%;
    height: auto;
    margin: 0;
  }
}